export const Home5Widget: React.FC = () => {
    const [expanded, setExpanded] = React.useState<any>(listFq[0].text);
    const handleChange = (panel: string) => (event: React.ChangeEvent<{}>, newExpanded: boolean) => {
        setExpanded(newExpanded ? panel : false);
    };
    return (
        <WapStyle>
            <LineWidget/>
            <h2 className={'title'}>
                <img src={faqImg} alt=""/>
            </h2>
            <div className='box'>
                {listFq.map((ev) => <CellWidget
                    handleChange={handleChange}
                    expanded={expanded}
                    panel={ev.title}
                    title={ev.title}
                    sub={ev.text}/>)}
            </div>


        </WapStyle>
    );
}


const CellWidget: React.FC<{
    title: string,
    sub: any,
    expanded: string,
    panel: string,
    handleChange: Function
}> = ({
          title,
          sub,
          panel,
          expanded,
          handleChange
      }) => {
    return (
        <CellStyle>
            <Accordion square expanded={expanded === panel} onChange={handleChange(panel)}>
                <AccordionSummaryStyle
                    expandIcon={<img src={dorwDwon}/>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <h3>{title}</h3>
                </AccordionSummaryStyle>
                <AccordionDetailsStyle>
                    <h4 dangerouslySetInnerHTML={{
                        __html: sub
                    }}>
                    </h4>
                </AccordionDetailsStyle>
            </Accordion>
        </CellStyle>
        // <CellStyle md={5} sm={12}>
        //   <h2>{title}</h2>
        //   <h3>{sub}</h3>
        // </CellStyle>
    );
}

