import {Grid} from "@material-ui/core";
import React, {useEffect, useState} from "react";
import styled from "styled-components";
import bgImg from "../assets/images/top_jgp.jpg"

const aa = require("../assets/voide/top.mp4").default;
export const VodieWidget: React.FC = () => {

    const [height, setHeight] = useState(810);
    const [width, setwidth] = useState(0);
    const [postion, setPostion] = useState({left: 0, top: 0})

    let minHeight = 810;
    let minWidth = 1440;
    useEffect(() => {
        // eslint-disable-next-line no-restricted-globals
        let _height = screen.height
        let _width;
        let _postion = {
            left: 0,
            top: 0,
        };

        _width = _height * 1.7777;
        if (_height < 500) {
            setHeight(minHeight);
            _postion.top = (minHeight - _height) / 2;
            setwidth(minWidth);
        } else {
            setHeight(_height);
            setwidth(_width);
            _postion.left = (_width - document.body.clientWidth) / 2;
        }

        setPostion(_postion);

    }, [])
    return (
        <VodieStyle>
            <video className={'video'} style={{
                height: height + "px",
                width: width + 'px',
                left: (0 - postion.left) + "px",
                top: (0 - postion.top) + "px"
            }} loop autoPlay muted playsInline>
                <source src={aa}/>
            </video>
            <div className='inner'>


            </div>

        </VodieStyle>
    );
}
const VodieStyle = styled.div`
  background-color: #000;
  position: relative;
  overflow: hidden;

  .inner {
    min-height: 100vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    max-width: 1500px;
    margin: 0 auto;
    padding: 20px 3%;
    box-sizing: border-box;
  }

  .left {
    position: relative;
    z-index: 30;

    h3 {
      font-size: 50px;
      font-weight: 700;
      color: #F0F0F0;
      text-align: start;

      span {
        font-weight: 700;
      }
    }

    h4 {
      max-width: 853px;
      font-size: 21px;
      font-weight: 400;
      color: #6A7582;
      margin: 50px 0 80px 0;
    }

    a {
      padding: 10px 40px;
      cursor: pointer;
      background: #1482E4;
      border-radius: 10px;
      font-size: 20px;
      color: #F0F0F0;
      text-decoration: none;
    }
  }


  .video {
    position: absolute;
  }

  @media (max-width: 600px) {
    padding: 0 5%;
    box-sizing: border-box;
    .left {
      h3 {
        font-size: 25px;
      }

      h4 {
        font-size: 12px;
        margin-top: 20px;
      }

      a {
        padding: 5px 20px;
        font-size: 16px;
      }
    }
  }
`
